'use client';
import { useState, useEffect } from'react';
// Update the import paths below to the correct relative paths for your project structure
import { Card } from '@/components/ui/card';
import { Heading } from '@/components/ui/heading';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Badge } from '@/components/ui/badge';
import { useRouter } from 'next/navigation';
import ProtectedRoute from '@/components/ProtectedRoute'
import { useAuth } from '@/context/AuthContext';
import axios from 'axios';

export default function Main() {
  const router = useRouter();
  const { isAuthenticated, logout } = useAuth();
  const [userName, setUsername] = useState('');
  // 模拟文献列表，实际应从后端获取
  const [documents, setDocuments] = useState([]);
  // 模拟AI助手对话内容
  const [aiAssistantMessages, setAiAssistantMessages] = useState([
    {
      text: '您好！我是您的AI研究助手。我可以帮助您分析文献、回答问题，让您的研究更高效。请问有什么我可以帮您的吗?',
      isAssistant: true
    }
  ]);
  const [userInput, setUserInput] = useState('');

  useEffect(() => {
    if (!isAuthenticated) return;
    const token = localStorage.getItem('token');
    axios.get('http://localhost:8000/api/user/me', {
      headers: {
        Authorization: `Bearer ${token}`
      }
    })
   .then(res => {
      setUsername(res.data.username);
   })
    .catch(() => {
      logout();
      router.push('/login');
   });
  }, [isAuthenticated, logout, router]);

    // 获取文献列表（可选）
    // axios.get('/api/documents', { headers: { Authorization: `Bearer ${token}` } })
    //   .then(res => setDocuments(res.data))
    //   .catch(() => setDocuments([]));

  const handleUploadDocument = () => {
    // 这里应实现上传文献的逻辑，例如调用后端API上传文件
    // 目前只是简单打印提示信息
    console.log('上传文献功能待实现');
  };

  const handleSendMessage = () => {
    if (userInput.trim() === '') return;
    setAiAssistantMessages((prevMessages) => [
     ...prevMessages,
      { text: userInput, isAssistant: false }
    ]);
    // 这里应实现向AI助手发送请求并获取回复的逻辑，例如调用后端API
    // 目前只是简单模拟回复
    setTimeout(() => {
      setAiAssistantMessages((prevMessages) => [
       ...prevMessages,
        {
          text: '这是AI助手的回复，实际功能待完善',
          isAssistant: true
        }
      ]);
      setUserInput('');
    }, 1000);
  };

  return (
    <ProtectedRoute>
        <div className="flex flex-col min-h-screen">
        {/* 顶部导航栏 */}
        <div className="flex justify-between items-center p-4 bg-white shadow">
            <div className="flex items-center">
            <Button variant="ghost" className="mr-2">
                AI+协同文献管理平台
            </Button>
            </div>
            <div className="flex-1">
            <Input
                placeholder="搜索文献..."
                className="w-full"
            />
            </div>
            <div className="flex items-center">
              {/* 用户名按钮，点击跳转到个人主页 */}
              <Button
                variant="ghost"
                className="mr-2"
                onClick={() => router.push('/home')}
                title="点击进入个人主页"
              >
                {userName}
              </Button>
              {/* 显示登录状态和退出按钮 */}
              {isAuthenticated && (
                <Button variant="outline" size="sm" onClick={logout}>
                  退出登录
                </Button>
              )}
            </div>
        </div>
        {/* 主体内容区域 */}
        <div className="flex flex-1">
            {/* 左侧文献库导航 */}
            <div className="w-64 bg-gray-100 p-4">
            <Heading as="h3" size="sm" className="mb-2">
                私人文献库
            </Heading>
            <Button variant="ghost" className="w-full text-left">
                <Badge variant="secondary" className="mr-2">
                ▼
                </Badge>
                全部文献
            </Button>
            <Heading as="h3" size="sm" className="mt-4 mb-2">
                公有文献库
            </Heading>
            <Button variant="ghost" className="w-full text-left">
                <Badge variant="secondary" className="mr-2">
                ▼
                </Badge>
                全部文献
            </Button>
            </div>
            {/* 右侧文献列表和AI助手区域 */}
            <div className="flex-1 p-4">
            <div className="flex justify-between items-center mb-4">
                <Heading as="h2" size="lg">
                课题组的文献
                </Heading>
                <Button variant="secondary" className="px-4 py-2">
                + 上传文献
                </Button>
            </div>
            {documents.length > 0 ? (
                documents.map((doc, index) => (
                <Card key={index} className="mb-4 p-4 bg-white shadow rounded">
                    <Heading as="h3" size="sm" className="mb-2">
                    {doc.title}
                    </Heading>
                    <p className="text-gray-600 text-sm mb-2">
                    作者: {doc.author}
                    </p>
                    <div className="flex space-x-2">
                    {doc.tags.map((tag, tagIndex) => (
                        <Badge key={tagIndex} variant="secondary" className="text-xs">
                        {tag}
                        </Badge>
                    ))}
                    </div>
                    <p className="text-gray-600 text-right text-xs">
                    {doc.date}
                    </p>
                </Card>
                ))
            ) : (
                <p className="text-gray-600">文献库为空</p>
            )}
            <div className="mt-8">
                <Heading as="h3" size="sm" className="mb-2">
                🤖 AI研究助手
                </Heading>
                {aiAssistantMessages.map((message, index) => (
                <div key={index} className={`flex items-start mb-2 ${message.isAssistant? 'justify-end' : ''}`}>
                    {message.isAssistant? (
                    <Badge variant="secondary" className="mr-2">
                        🤖
                    </Badge>
                    ) : (
                    <Badge variant="secondary" className="mr-2">
                        👤
                    </Badge>
                    )}
                    <div className={`bg-gray-100 p-2 rounded ${message.isAssistant? 'rounded-l-none' : 'rounded-r-none'}`}>
                    {message.text}
                    </div>
                </div>
                ))}
                <div className="flex items-center mt-2">
                <Input
                    placeholder="请输入您的问题..."
                    className="flex-1 p-2"
                    value={userInput}
                    onChange={(e) => setUserInput(e.target.value)}
                />
                <Button variant="secondary" className="px-4 py-2 ml-2" onClick={handleSendMessage}>
                    发送
                </Button>
                </div>
            </div>
            </div>
        </div>
        </div>
    </ProtectedRoute>
  );
}